@charset "utf-8";
/* CSS Document */
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*, *:after, *:before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

body {
	background-color: #fff;
}

.flex-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.container {
    display: block;
    width: 100%;
}

.content {
    width: 90%;
    margin: 0 auto;
}


.text-center {
    text-align: center;
}

.main-header {
    background-color: darkcyan;
    padding: 20px 0;
    color: #fff;
}

.shelves {
    margin: 40px 0 50px;
}

.shelf-container {
    margin-bottom: 20px;
}

.shelf-container h2 {
    border-bottom: 1px solid #ddd;
    color: #5f9ea0;
}

.book-container {
    margin: 20px 0;
}

.book-container h3 {
    font-size: .8em;
    width: 80%;
}

.book-container {
    width: 45%;
    margin-right: 5%;
}

.book-container:last-of-type {
    margin-right: 0%;
}

.book-content > p {
    font-size: .8em;
    font-style: italic;
    color: cadetblue;
}

.book-content img {
    width: 100%;
}

/* ----- STYLE SELECT ELEMENT TO A BUTTON ---------*/
.book-select-container {
    position: relative;
    width: 35px;
    float: right;
}

select {
    position: absolute;
    top: 0;
    border: 0 !important;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance:none;
    width: 100%;
    height: 35px;
}
  
select:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 #000;
}

select::-ms-expand {
    display: none;
}
  
.book-select-icon {
    background: #FFF url('../src/arrow-icon.png') no-repeat right;
    right:0px;
    top:0px;
    width:35px;
    height:35px;
    position:absolute;
    display:block;
    z-index: 10;
    margin: 0 0 0 0;
    pointer-events:none;
}

/* BTN TO NAVIGATE TO SEARCH PAGE */
.nav-main-btn {
    position: fixed;
    bottom: 10px;
    right: 10px;
    padding: 15px 17px;
    font-size: 20px;
    background-color: brown;
    color: #fff;
    text-decoration: none;
    border-radius: 50%;
    z-index: 20;
}

/* SEARCH INPUT */
.search-books--content {
    padding: 10px 0;
}

.search-books--input {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: none;
    border-bottom: 2px solid #ccc;
    font-size: 16px;
    background-color: white;
    background-image: url('../src/searchicon.png');
    background-position: 8px 8px; 
    background-repeat: no-repeat;
    padding: 12px 20px 12px 40px;
}

.list-of-books-found {
    margin-bottom: 50px;
}


@media only screen and (min-width: 490px) and (max-width: 768px) {
    .book-container {
        width: 30%;
        margin-right: 3%;
    }
    
    .book-container:nth-child(3n+3) {
        margin-right: 0%;
    }
}

@media only screen and (min-width: 769px) and (max-width: 1119px)  {
    .book-container {
        width: 22%;
        margin-right: 3%;
    }
    
    .book-container:nth-child(4n+4) {
        margin-right: 0%;
    }
}

@media only screen and (min-width: 1120px) {
    .book-container {
        width: 14%;
        margin-right: 2%;
    }
    
    .book-container:nth-child(6n+6) {
        margin-right: 0%;
    }
}



